import "core-js/modules/es.array.iterator.js";
import "core-js/modules/es.map.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/es.string.iterator.js";
import "core-js/modules/web.dom-collections.iterator.js";
import "core-js/modules/es.regexp.to-string.js";
import "core-js/modules/es.array.find.js";
import "core-js/modules/es.array.from.js";
// components
import VimeoPlayerButton from "Shared/ts/components/vimeo-player-button";
import Nav from "Shared/ts/components/nav";
import Carousel from "Shared/ts/components/carousel";
import MediaPlayer from "Shared/ts/components/media-player"; // adapters

import SlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/slide-carousel";
import VimeoMediaPlayerAdapter from "Shared/ts/api/media-player/vimeo-media-player"; // observers

import { observer } from "shared/ts/observers/intersection"; // utils

import LoadItem from "Shared/ts/utils/load-item";
import VimeoManager from "Shared/ts/utils/vimeo-manager";
import { elementExists } from "shared/ts/utils/html";
import CaptureElement from "Shared/ts/utils/capture-element";
new Nav("nav");
var vimeoManager = new VimeoManager();
var vimeoPosterImageRepository = new Map();
var vimeoPlayerButton = new VimeoPlayerButton({
  imageSpecs: {
    width: "1920",
    height: "1080",
    quality: "70"
  }
});

var createMediaPlayerByPlaceholder = function createMediaPlayerByPlaceholder(placeholder) {
  var parent = placeholder.closest(".media-player");
  new MediaPlayer(new VimeoMediaPlayerAdapter(parent));
};

var updateImageBySource = function updateImageBySource(image, source) {
  if (elementExists(image)) {
    image.setAttribute("src", source);
  }
};

var updateVimeoPosterImageById = function updateVimeoPosterImageById(image, id) {
  var poster = vimeoPosterImageRepository.get(id);

  if (!poster) {
    vimeoManager.generatePosterImage(id.toString(), vimeoPlayerButton.imageSpecs).then(function (source) {
      vimeoPosterImageRepository.set(id, source);
      updateImageBySource(image, source);
    });
  }

  if (poster) {
    updateImageBySource(image, poster);
  }
};

var updateVimeoPosterImageByPlayer = function updateVimeoPosterImageByPlayer(poster, player) {
  player.on("loaded", function (data) {
    return updateVimeoPosterImageById(poster, data.id);
  });
};

vimeoPlayerButton.initializeOnCapture(function (response) {
  var capturePlaceholder = new CaptureElement(response.placeholder);
  capturePlaceholder.subscribe("childList", function (record) {
    var image = Array.from(record.addedNodes).find(function (node) {
      return node.nodeName.toUpperCase() === "IMG";
    });

    if (image) {
      capturePlaceholder.disconnect();
      updateVimeoPosterImageByPlayer(image, response.player);
    }
  });
  createMediaPlayerByPlaceholder(response.placeholder);
});
observer("[data-src-img]", {
  inRange: function inRange(record) {
    return new LoadItem(record);
  }
});
observer("[data-src-iframe]", {
  inRange: function inRange(record) {
    new LoadItem(record, {
      tag: "iframe",
      src: "data-src-iframe"
    });
  }
});
observer(".slide", {
  inRange: function inRange(record) {
    var carousel = new Carousel(new SlideCarouselAdapter(record));
    carousel.enablePrevNextControls();
  }
});